@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.form__title {
  width: 100%;
  height: 10rem;

  margin: 0.75rem 0;

  border: 1px solid var(--gray-4);
  border-radius: 0.5rem;
}

.form__title__img {
  width: 100%;
  height: 100%;
}

.form__about {
  margin-bottom: 12px;
  border-top: 11px solid var(--red-5);
  border-radius: 8px;
  background-color: white;
  padding: 12px 24px 24px;
  line-height: 20px;
  letter-spacing: 0.2px;
  word-wrap: break-word;
  color: var(--gray-9);
  font-size: 14px;
  font-weight: 400;
}
.form__about__title {
  margin-bottom: 12px;
  line-height: 135%;
  font-family: "Google Sans", Roboto, Helvetica, Arial, sans-serif;
  font-size: 32px;
  font-weight: 400;
}
.form__about__legend {
  margin-top: 12px;
  color: var(--red-7);
}

.form__input-field {
  margin-bottom: 12px;
  border: 1px solid var(--gray-4);
  border-radius: 8px;
  background-color: white;
  padding: 1.5rem;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.form__input-field.error {
  border-color: var(--red-7);
}

.form__input-field__header {
  margin-bottom: 16px;
}

.form__input-field__label {
  line-height: 24px;
  font-size: 1rem;
}

.form__input-field__asterisk {
  margin-left: 6px;
  color: var(--red-7);
  font-size: 12px;
}

.form__input-field__description {
  margin-top: 4px;
  font-size: 12px;
}

.form__input-field__container {
  display: flex;
  position: relative;
  align-items: flex-end;
  transition: border 0.3s ease-in-out;
  border-bottom: 1px solid var(--gray-4);
  width: 50%;
  height: 40px;
  font-size: 14px;
}

.form__input-field__container.focus {
  border-bottom-width: 3px;
  border-bottom-color: var(--red-7);
}

.form__input-field.error .form__input-field__container {
  border-color: var(--red-7);
}

.form__input-field__input {
  margin-bottom: 8px;
  outline: 0;
  border: 0;
  width: 100%;
}

.form__input-field__message {
  display: flex;
  align-items: center;
  margin-top: 8px;
  color: var(--red-7);
  font-size: 12px;
}

.form__input-field__message::before {
  display: block;
  margin-right: 12px;
  background-image: url(/funding-x_task/static/media/error_icon.f0af6c6f.svg);
  background-repeat: no-repeat;
  background-size: 100%;
  width: 20px;
  height: 20px;
  content: "";
}
