.form__submit-btn {
  padding: 0 24px;
  border: 0;
  border-radius: 4px;
  background-color: var(--red-6);
  font-size: 14px;
  font-weight: 500;
  line-height: 36px;
  color: white;

  cursor: pointer;
}

.form__submit-btn:hover {
  box-shadow: 0px 2px 1px -1px rgb(232 34 34 / 20%),
    0px 1px 1px 0px rgb(232 34 34 / 14%), 0px 1px 3px 0px rgb(232 34 34 / 12%);
}

.footer {
  margin-top: 20px;
  font-size: 12px;
  color: var(--gray-6);
}

.footer__warning {
  letter-spacing: 0.3px;
  line-height: 16px;
}

.footer__disclaimer {
  margin: 16px 0 10px;
  text-align: center;
}

.footer__links {
  text-align: center;
}

.footer__link {
  display: inline-block;
  margin: 4px;
  color: var(--gray-6);
  word-break: keep-all;
  word-wrap: normal;
}

.footer__product-name {
  margin: 24px 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.footer__product-name__img {
  margin-right: 4px;
  width: 74px;
  height: auto;
}

.footer__product-name__text {
  font-size: 22px;
  line-height: 22px;
  color: hsl(220, 9%, 73%);
}

/* import Fonts */ /* Robot */ /* Google Sans */
/* default styles */
html {
  font-size: 16px;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  font-family: inherit;
  box-sizing: inherit;
}

/* variables */
:root {
  /* red-color */
  --red-3: hsl(0, 100%, 96%);
  --red-5: hsl(0, 100%, 75%);
  --red-6: hsl(0, 81%, 52%);
  --red-7: hsl(4, 71%, 50%);

  /* gray colors */
  --gray-4: hsl(220, 9%, 87%);
  --gray-6: hsla(0, 0%, 0%, 0.66);
  --gray-9: hsl(225, 6%, 13%);
}

/* helper classes */
.wrapper {
  width: 100%;
  max-width: 640px;
}

/* user styles */
.App {
  display: flex;
  justify-content: center;
  background-color: hsl(0, 100%, 96%);
  background-color: var(--red-3);
  padding: 0 1.5rem;
  width: 100%;
  min-height: 100vh;
}

